<template>
  <div class="dialog poolinfo w-80" v-if="value">
    <div class="dialog-header">卡池內容<span class="dismiss" @click="dismiss">X</span></div>
    <div class="dialog-body">
        <div class="group-row" v-for="(group,index) in poolGroups" :key="index">
            <div class="group-name" :style="getHeaderStyle(group)">
              <span class="title">
                {{group.name}}
              </span>
              <span class="subtitle">出貨率：{{group.rate}}</span>
              </div>
            <div class="group-cards">
                <gamecard v-for="(card, index) in getCards(group)" :key="index" :value="card"></gamecard>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
// import {mapGetters} from 'vuex';
import CompCard from '@/components/card/Card.vue';
export default {
  props: {
    value: {
      type: Boolean
    },
    pool:{
        type:Object
    }
  },
  components:{
      'gamecard':CompCard
  },
  data() {
    return {
    };
  },
  computed: {
      poolGroups(){
        var cardbox = this.pool.cardBox;
          return this.pool.groups.map(g=>{
            g.model = cardbox.groups.find(m=>m.id == g.groupId);
            return g;
          });
      }
  },
  methods: {
    dismiss() {
        this.$emit('input',false);
    },
    getCards(group){
        var cardKey = group.cards;
        var cards = this.pool.cardBox.cards.filter(c=>cardKey.findIndex(k=>c.id == k) >= 0);
        return cards;
    },
    getHeaderStyle(group){
      return{'border-bottom':'2px solid' + group.model.color}
    }
  }
};
</script>

<style lang="scss">
.poolinfo{

  .dialog-body{
    overflow: auto;
    padding: 5px;
    height: 50rem;
    text-align: left;
  }

  .group-name{
    font-size:1.2rem;
  }

  .subtitle{
    font-size:1rem;
  }

}
</style>