<template>
  <div class="config-cardgroup">
    <div class>
      <div class="table cardgroup-table">
        <div class="table-header">
          <span class="table-col c-id">編號</span>
          <span class="table-col c-name">名稱</span>
          <span class="table-col c-rate">預設機率</span>
          <span class="table-col c-color">顏色</span>
          <span class="table-col c-button"></span>
        </div>
        <div class="table-body">
          <cardgroup-row
            v-for="group in groups"
            :key="group.id"
            :value="group"
            :mode="'edit'"
            @remove="remove"
          ></cardgroup-row>
          <div class="row" v-if="!isCreating">
            <div class="table-col">
              <button class="btn" @click="add">新增</button>
            </div>
          </div>
          <cardgroup-row
            v-else
            class
            :value="editingGroup"
            :status="{'mode':'edit'}"
            @commit="onCreate"
            @cancel="onCreateCancel"
          ></cardgroup-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardGroupRow from "./CardGroupRow";
export default {
  components: {
    "cardgroup-row": CardGroupRow
  },
  data() {
    return {
      targetGroup: {},
      editingGroup: null,
      isCreating: false
    };
  },
  methods: {
    add() {
      this.editingGroup = {};
      this.isCreating = true;
    },
    remove(group) {
      let index = this.groups.findIndex(e => e == group);
      if (index === 0 || index > 0) {
        this.groups.splice(index, 1);
      }
    },
    onCreate(group) {
      this.$store.state.cardBox.addGroup(group);
      this.editingGroup = null;
      this.isCreating = false;
    },
    onCreateCancel() {
      this.isCreating = false;
      this.editingGroup = null;
    }
  },
  computed: {
    groups() {
      if (this.$store.state.cardBox) return this.$store.state.cardBox.groups;
      else return [];
    }
  }
};
</script>

<style lang="scss">
.cardgroup-config {
  .cardgroup {
    display: flex;
  }
  .label {
    font-size: 0.9rem;
    font-weight: bold;
  }
}

.cardgroup-table {
  .c-id {
    width: 50px;
  }
  .c-name {
    width: 20%;
    flex-grow: 1;
  }
  .c-rate {
    width: 10%;
  }
  .c-color {
    width: 10%;
    flex-grow: 1;
  }
  .c-button {
    width: 200px;
  }
}
</style>