<template>
  <div :class="styleClass" :style="backgroundStyle">
      <div class="img-wrapper" v-if="value.image">
          <img class="img" :src="value.image" :alt="value.name">
      </div>
      <div class="gamecard-title">{{value.name}}</div>
  </div>
</template>

<script>
export default {
    props:{
        value:{
            type:Object
        },
        type:{
            type:String,
        }
    },
    computed:{
        backgroundStyle(){
            return{
                background: this.value._group ? this.value._group.color : '#AAAAAA'
            };
        },
        styleClass(){
            switch(this.type)
            {
                case 'min':
                    return 'gamecard-min';
                default:
                    return 'gamecard';
            }
        }
    }
}
</script>

<style lang="scss">
@import '@/assets/mixin';

.gamecard-min {
  display: inline-flex;
  padding: 5px;
  border: 1px solid;
  margin: 5px;
  width: 80px;
  vertical-align: middle;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.5s all;
}

.gamecard{
    @include inline-flex;
    padding: 5px;
    border:1px solid;
    margin: 5px;
    height: 100px;
    width:80px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 0.5s all;

    .img-wrapper{
        line-height: 60px;
        width:60px;
        height: 60px;
        position: relative;
        background: #222;
        white-space: nowrap;
    }

    .img{
        width:100%;
        height: 100%;
        border: none;
    }
}
</style>