<template>
  <div class="side-form card-from row">
    <template v-if="action == 'edit' || action == 'create'">
      <div class="form-header">
        <h2>{{title}}</h2>
      </div>
      <div class="form-body">
        <div class="img-wrapper gamecard-img">
          <img class :src="image" alt />
        </div>
        <div class="control-group row">
          <div class="col w-25">編號</div>
          <div class="col w-75">
            <input type="text" class="w-100 form-control" v-model="id" />
            <invalid-message :value="validation.id" />
          </div>
        </div>
        <div class="control-group row">
          <div class="col w-25">名稱</div>
          <div class="col w-75">
            <input type="text" class="w-100 form-control" v-model="name" />
            <invalid-message :value="validation.name" />
          </div>
        </div>
        <div class="control-group row">
          <div class="col w-25">群組</div>
          <div class="col w-75">
            <select class="w-100 form-control" v-model="group">
              <option :value="group.id" v-for="group in cardGroups" :key="group.id">{{group.name}}</option>
            </select>
            <invalid-message :value="validation.group" />
          </div>
        </div>
        <div class="control-group row">
          <div class="col w-25">圖片</div>
          <input type="text" class="form-control w-75" v-model="image" />
        </div>
      </div>
      <div class="form-footer">
        <button class="btn btn-primary" @click="commit">確定</button>
        <button class="btn" @click="cancel">取消</button>
        <button class="btn btn-danger" @click="remove">刪除</button>
      </div>
    </template>
    <template v-else>
      <div class="form-body">
        <button class="btn" @click="create">新增</button>
      </div>
    </template>
  </div>
</template>

<script>
import InvalidMsg from "@/components/form/InvalidMessage";
import { FxValidator } from "@/scripts/validator/index";
import { mapGetters } from "vuex";
export default {
  components: {
    "invalid-message": InvalidMsg
  },
  props: {
    value: {
      type: Object
    }
  },
  data() {
    const that = this;
    let validator = new FxValidator();
    let validationConfig = {
        name: {
          rules: {
            required:{},
          }
        },
        id:{
          rules:{
            required:{},
            without:{
              value:()=>that.$store.getters.cards.map(c=>c.id).filter(c=>c != that.value.id),
              message:'該ID已經被占用'
            }
          }
        },
        group:{
          rules:{
            required:{},
            within:{
              value:()=>that.$store.getters.cardGroups.map(g=>g.id),
              message:'請選擇一個群組'
            }
          }
        },
      };
    let data = {
      id: "",
      name: "",
      group: "",
      image: "",
      action: '',
      validation: validationConfig,
      validator:validator
    };
    validator.bind(()=>data,data.validation);
    return data;
  },
  computed: {
    title() {
      switch (this.action) {
        case "create":
          return "新增";
        case "edit":
          return "變更";
      }
      return "";
    },
    cardGroups() {
      return this.$store.getters.cardGroups;
    },
    ...mapGetters(['cardGroups','cards'])
  },
  methods: {
    commit() {
      let result = this.validator.validate();
      if (result) {
        let current = {
            id:this.id,
            name:this.name,
            group:this.group,
            image:this.image
          };
        if (this.action == "create") {
          this.$store.state.cardBox.addCard(current);
        } else if (this.action == "edit") {
          Object.assign(this.target,current);
          this.target = null;
        } else this.$emit("commit", this.value);
        this.action = '';
        this.clean();
      }
    },
    cancel() {
      this.target = null;
      this.clean();
      this.action = '';
      this.$emit("cancel");
    },
    create() {
      this.$emit('input','');
      this.clean();
      this.target = null;
      this.action = 'create';
    },
    clean(){
      this.name = '';
      this.group = '';
      this.id = '';
      this.image = '';
      this.$emit('input',{});
    },
    remove(){
      this.$store.state.cardBox.removeCard(this.id);
      this.clean();
      this.action = '';
    }
  },
  watch:{
    value(to){
      this.target = this.cards.find(m=>m.id == to.id);
      if(this.target)
      {
        var model = this.target.toModel();
        this.id = model.id;
        this.name = model.name;
        this.group = model.group;
        this.image = model.image;
        this.action = 'edit';
      }
    }
  }
};
</script>

<style lang="scss">
.side-form {
  background: #222;
  color: #fff;
  top: 100px;
  height: 500px;
  justify-content: center;

  .form-body {
    padding: 10px;
    align-content: center;
  }
}
</style>