<template>
  <div class="config-pool row">
    <div class="col w-25 pool-list">
      <div class="row block">
        <button class="btn" @click="create">新增</button>
      </div>
      <div class="row block" v-for="pool in pools" :key="pool.index" @click="open(pool)">
        {{pool.name}}
      </div>
    </div>
    <div class="col w-75 pool-content">
      <div class="row" v-if="currentPool">
        <div class="col w-25 block">
          <div class="control-group row">
            <div class="col w-25">編號</div>
            <div class="col w-75">
              <input type="text" class="form-control w-100" v-model="currentPool.id"/>
              <invalid-message class="w-100" :value="validation.id" />
            </div>
          </div>
          <div class="control-group row">
            <div class="col w-25">名稱</div>
            <div class="w-75">
              <input type="text" class="form-control w-100" v-model="currentPool.name"/>
              <invalid-message class="w-100" :value="validation.name" />
            </div>
          </div>
          <div class="control-group">
            <button class="btn" @click="commit">儲存</button>
            <button class="btn" @click="cancel">取消</button>
          </div>
        </div>
        <div class="table w-75">
          <div class="table-header">
            <div class="w-20">群組</div>
            <div class="w-20">機率</div>
            <div class="w-60"></div>
          </div>
          <div class="table-body">
            <div class="table-row block" v-for="group in currentPool.groups" :key="group.id">
              <div class="table-col w-20">{{group._groupInfo.name}} <span class="t-error" v-if="group._isUnexistedGroup">無效的群組</span> </div>
              <div class="table-col w-20">
                <input type="decimal" class="form-control w-75" v-model="group.rate" />
              </div>
              <div class="table-col w-60">
                <card-wrapper v-for="(card, id) in getCardsOfGroup(group)" :key="id" :active="card.selected">
                  <card :value="card" :type="'min'" @click.native="toggleCard(id,group)"></card>
                </card-wrapper>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CompCard from "@/components/card/Card";
import Pool from "@/scripts/pool/pool";
import CardWrapper from "@/components/card/CardWrapper"
import {mapGetters} from "vuex"
import { FxValidator } from "@/scripts/validator/index";
import CompInvalidMsg from "@/components/form/InvalidMessage";

export default {
  components: {
    card: CompCard,
    'card-wrapper':CardWrapper,
    'invalid-message':CompInvalidMsg
  },
  data() {
    var data = {
      //用於編輯的
      currentPool:null,
      //編輯的對象
      targetPool:null,
      validator:new FxValidator(),
      validation:{
        id:{
          rules:{
            required:{},
            without:{
              value:()=>this.pools.map(p=>p.id).filter(c=>c != data.currentPool.id),
              message:'該id已被占用'
            }
          }
        },
        name:{
          rules:{
            required:{}
          }
        }
      }
    };
    data.validator.bind(()=>data.currentPool, data.validation);
    return data;
  },
  computed: {
    cardGroups() {
      return this.$store.state.groups;
    },
    cardList() {
      console.log("u");
      return this.$store.state.cards;
    },
    groupsCards() {
      let result = {};
      for (let i = 0; i < this.$store.state.cards.length; i++) {
        const card = this.$store.state.cards[i];
        const group = card.attributes.group;
        if (!result[group]) {
          result[group] = [];
        }
        result[group].push(card);
      }
      return result;
    },
    ...mapGetters(['pools'])
  },
  methods: {
    open(pool){
      var poolForEdit = new Pool(pool.toModel());
      poolForEdit.cardBox = pool.cardBox;
      poolForEdit.loadCardboxGroups();
      this.currentPool = poolForEdit;
      this.targetPool = pool;
    },
    create(){
      var pool = new Pool();
      pool.cardBox = this.$store.state.cardBox;
      pool.loadCardboxGroups();
      this.currentPool = pool;
      this.targetPool = null;
    },
    commit(){
      if(!this.validator.validate())
      {
        return;
      }
      var model = this.currentPool.toModel();
      if(this.targetPool)
      {
        this.targetPool.assign(model);
      }
      else{
        this.pools.push(this.currentPool);
      }
      this.currentPool = null;
      this.targetPool = null;
    },
    cancel(){
      this.currentPool = null;
      this.targetPool = null;
    },
    toggleCard(id,group){
      var index = group.cards.findIndex(m=>m == id);
      if(index >= 0)
      {
        group.cards.splice(index,1);
      }
      else{
        group.cards.push(id);
      }
    },
    getCardsOfGroup(group){
      var result = {}

      group._cardsInBox.forEach(item => {
        result[item.id] = {
          name:item.name,
          selected:false,
          _group:item._group
        };
      });

      group.cards.forEach(id=>{
        if(result[id])
        {
          result[id].selected = true;
        }
        else{
          result[id] = {
            name:'('+id+')',
            isDeprecated:true,
            _group:group._groupInfo
          }
        }
      });

      return result;

    }
  },
  filters: {
    isCardSelected(card,group){
      var index = group.cards.findIndex(m=>m == card.id);
      return index >= 0;
    }
  },
  watch:{

  },
  mounted(){
  }
};
</script>

<style lang="scss">
.c-pool-meta {
  width: 200px;
  flex-grow: 0;
}

.block {
  background: #333;
  margin: 5px;
  padding: 5px;
}

.pool-groups {
  overflow: auto;
}
</style>