<template>
  <div class="pool">
    <div class="stage">
      <gamecard v-for="(item,index) in stage" :key="index" :value="item" />
    </div>
    <div class="panel">
      <div class="panel-body">
        <button class="btn" @click="draw(1)">單抽</button>
        <button class="btn" @click="draw(10)">十連抽</button>
        <button class="btn" @click="draw(100)">百連抽</button>
        <button class="btn" @click="clear">清除</button>
        <button class="btn" @click="showInfo">卡池資訊</button>
      </div>
    </div>
    <div class="panel">
      <div class="panel-body">
        <div
          v-for="group in statistic"
          :key="group.attributes.id"
        >{{group.attributes.name}}:{{group.count}}</div>
      </div>
    </div>
    <div class="panel">
      <div class="panel-header">歷史紀錄</div>
      <div class="panel-body history">
        <gamecard v-for="(item,index) in history" :key="index" :value="item" />
      </div>
    </div>
    <poolinfo v-model="infoDialog" :pool="pool" />
  </div>
</template>

<script>
import CompCard from "@/components/card/Card.vue";
import CompPoolInfo from "./partial/PoolInfo.vue";

export default {
  components: {
    gamecard: CompCard,
    poolinfo:CompPoolInfo
  },
  data() {
    return {
      stage: [],
      history: [],
      drawnCardCount: 0,
      pool: null,
      cardBox: null,
      infoDialog:false
    };
  },
  methods: {
    draw(length = 1) {
      this.stage.splice(0, this.stage.length);
      this.pool.removeInvalidCards();
      for (let i = 0; i < length; i++) {
        var cardid = this.pool.choose();
        if (cardid) {
          var card = this.pool.cardBox.getCard(cardid);
          if(!card)
          {
            console.error('An invalid card appear, this card will be discarded');
            continue;
          }
          this.stage.push(card);
          this.history.push(card);
          this.drawnCardCount++;
        } else {
          alert('該池沒有可以抽取的卡片')
          break;
        }
      }
    },
    clear() {
      this.stage.splice(0, this.stage.length);
      this.history.splice(0, this.history.length);
    },
    showInfo(){
      this.infoDialog = true;
    }
  },
  computed: {
    statistic() {
      var result = {};
      this.$store.getters.cardGroups.forEach(g=>{
        result[g.id] = {
          attributes:g,
          count:0
        }
      });
      for (let i = 0; i < this.history.length; i++) {
        const group = this.history[i]._group;
        if (result[group.id]) {
          result[group.id].count += 1;
        }
      }
      return result;
    }
  },
  mounted() {
    var id = this.$route.params.id;
    var pool = this.$store.getters.pools.find(m => m.id == id);
    if (pool) {
      return (this.pool = pool);
    } else {
      this.$router.push({ name: "Home" });
    }
  }
};
</script>

<style lang="scss">
.pool{
.stage {
  height: 500px;
  background: #111;
  overflow: auto;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.history {
  height: 500px;
  overflow: auto;
}
}

</style>