<template>
  <div class="config-cardbox row">
    <card-edit-form
      class="cardbox-form col w-25 block"
      v-model="currentCard"
      :action="formStatus"
      @cancel="abortEdit"
      @create="create" 
      @edit="update" 
      @showCreate="add"
    />
    <div class="cardbox-list col w-75">
      <card-wrapper v-for="item in cardList" :key="item.id" :active="targetCard==item">
        <card  :value="item" @click.native="edit(item)" />
      </card-wrapper>
    </div>
  </div>
</template>

<script>
import CompCard from "@/components/card/Card";
import CompEditForm from "./CardEditForm";
import CompCardWrapper from "@/components/card/CardWrapper";
import Card from "@/scripts/pool/card";

export default {
  props: {
    cards: {
      type: Array
    }
  },
  components: {
    card: CompCard,
    "card-edit-form": CompEditForm,
    "card-wrapper": CompCardWrapper
  },
  data() {
    var groups = this.$store.state.groups;
    return {
      nextId: 0,
      currentCard: null, //use for edit form
      targetCard:null, //store update target
      showEditModal: false,
      cardGroups: groups,
      formStatus:'',
    };
  },
  computed: {
    cardList() {
      return this.$store.state.cardBox.cards;
    }
  },
  methods: {
    add() {
      console.log("add");
      this.currentCard = new Card();
      this.targetCard = {};
      this.formStatus = "create";
    },
    edit(item) {
      this.currentCard = Object.assign({},item);
      this.targetCard = item;
      this.showEditModal = true;
      this.formStatus = 'edit';
    },
    update() {
      Object.assign(this.targetCard,this.currentCard);
      this.deactiveEdit();
    },
    create(card){
        this.$store.commit('addCard',card);
        this.nextId++;
        this.deactiveEdit();
    },
    abortEdit() {
      this.deactiveEdit();
    },
    deactiveEdit(){
      this.formStatus = 'none';
      this.currentCard = {};
      this.targetCard = null;
    }
  },
  mounted() {

  }
};
</script>

<style>


.cardbox {
  display: flex;
}
</style>