<template>
  <div class="table-row cardgroup">
    <template v-if="status.mode=='edit'">
      <div class="table-col c-id">
        <input type="text" class="w-100 form-control" v-model="editingGroup.id" />
        <invalid-message class="w-100" :value="validation.id" />
      </div>
      <div class="table-col c-name">
        <input type="text" class="row w-100 form-control" v-model="editingGroup.name" />
        <invalid-message class="w-100" :value="validation.name" />
      </div>
      <div class="table-col c-rate">
        <input type="text" class="row w-100 form-control" v-model="editingGroup.rate" />
        <invalid-message class="w-100" :value="validation.rate" />
      </div>
      <div class="table-col c-color">
        <div class="row w-100">
          <div class="col w-20" :style="{background:editingGroup.color}"></div>
          <input type="text" class="form-control col w-80" v-model="editingGroup.color" />
        </div>
        <invalid-message class="w-100" :value="validation.color" />
      </div>
      <div class="table-col c-button">
        <button class="btn" @click="commit()">確認</button>
        <button class="btn" @click="cancel()">取消</button>
      </div>
    </template>
    <template v-else>
      <div class="table-col c-id">
        {{value.id}}
        </div>
      <div class="table-col c-name">
        {{value.name}}
      </div>
      <div class="table-col c-rate">{{value.rate}}
      </div>
      <div class="table-col c-color">
        <div class="row">
          <div class="col w-20" :style="{background:value.color}"></div>
          <div class="col w-80">{{value.color}}</div>
        </div>
      </div>
      <div class="table-col c-button">
        <button class="btn" @click="edit()">編輯</button>
        <button class="btn" @click="remove()">刪除</button>
      </div>
    </template>
  </div>
</template>

<script>
import { FxValidator } from "@/scripts/validator/index";
import InvalidMsg from "@/components/form/InvalidMessage";

export default {
  components: {
    'invalid-message': InvalidMsg
  },
  props: {
    value: {
      type: Object
    },
    status: {
        type:Object,
        default(){
            return {
                mode:'view'
            }
        }
    }
  },
  data() {
    this.$validator = new FxValidator()
    const that = this;
    let data = {
      editingGroup: {},
      validation:{
        id:{
          rules:{
            required:{},
            without:{
              value:()=>that.$store.getters.cardGroups.map(g=>g.id).filter(g=>g != that.value.id),
              message:'該ID已經被占用'
            }
          }
        },
        name:{
          rules:{
            required:{},
          }
        },
        rate:{
          rules:{
            max:{value:1},
            min:{value:0}
          }
        },
        color:{
          rules:{
            regex:{
              value:/^#([0-9A-Fa-f]{3,3})([0-9A-Fa-f]{3,3})?$/,
              message:'不是顏色的格式'
              },
            
          }
        }
      },
    };
    this.$validator.bind(()=>that.editingGroup,data.validation);
    return data;
  },
  methods: {
    edit() {
      this.editingGroup = Object.assign({}, this.value);
      this.status.mode = 'edit';
      this.$emit("startEdit", this.value);
    },
    finishEdit(){
      this.editingGroup = null;
      this.status.mode = 'view';
    },
    remove() {
      this.$emit("remove", this.value);
    },
    cancel() {
      this.finishEdit();
      this.$emit("cancel", this.value);
    },
    commit() {
      var result = this.$validator.validate();
      console.log(result);
      if(!result)
      {
        return;
      }
      if (this.editingGroup) {
        Object.assign(this.value, this.editingGroup);
        this.$emit("commit", this.editingGroup);
      } else {
        console.error("Group is not existed");
      }
      this.finishEdit();
    }
  },
  watch:{
      status(to){
          if(to.mode == 'edit')
          {
              this.edit();
          }
          else{
            this.finishEdit();
          }
      }
  }
};
</script>

<style lang="scss">
.color-chip{
    vertical-align: middle;
    height: 100%;
    &:after{
      content:' '
    }
}
</style>