<template>
  <div class="page">
    <div class="page-title">{{currentGameName}}</div>
      <menu-panel :items="pools" :title="'卡池'" @select="enterPool" />
  </div>
</template>

<script>
import CompMenuPanel from "@/components/common/MenuPanel";
import {mapGetters} from 'vuex';
export default {
  data() {
    return {};
  },
  methods: {
    enterPool(pool) {
      this.$router.push({ name: "pool", params: { id: pool.id } });
    }
  },
  computed: {
    currentGameName(){
      var currentGame = this.$store.state.currentGame;
      if(currentGame)
      {
        return currentGame.name;
      }
      else{
        return '-'
      }
    },
    ...mapGetters(["pools"])
  },
  components: {
    "menu-panel": CompMenuPanel
  }
};
</script>

