<template>
  <div>
    <div class="panel">
      <div class="panel-header">卡池</div>
      <div class="panel-body">
        <card-pool></card-pool>
      </div>
    </div>
    <div class="panel">
      <div class="panel-header">卡片</div>
      <div class="panel-body">
        <card-box :cards="cardList" />
      </div>
    </div>
    <div class="panel">
      <div class="panel-header">群組</div>
      <div class="panel-body">
        <div class="section">
          <card-group></card-group>
        </div>
      </div>
    </div>
    <div class="panel">
      <div class="panel-body">
        <button class="btn" @click="saveGame">儲存</button>
        <button class="btn" @click="loadGame(true)">載入自訂</button>
        <button class="btn" @click="loadGame(false)">載入預設</button>
      </div>
    </div>
  </div>
</template>

<script>
import CardBox from "./partial/CardBox";
import CardGroup from "./partial/CardGroup";
import CardPool from "./partial/CardPool";
import { mapMutations,mapState } from 'vuex'

export default {
  components: {
    "card-box": CardBox,
    "card-group": CardGroup,
    "card-pool": CardPool
  },
  data() {
    return {
      cardList: []
    };
  },
  computed:{
    ...mapState(['cards'])
  },
  methods:{
    saveGame(){
      this.$store.dispatch('saveGame');
    },
    loadGame(loadCustom){
      var data = {
        id:this.$store.state.currentGame.id,
        loadCustom:loadCustom == true
      };
      this.$store.dispatch('loadGame',data);
    },
    ...mapMutations(['saveConfig','loadConfig','loadDefaultConfig']),
    
  },
  mounted(){
  }
};
</script>

