<template>
  <div class="invalid-message" v-if="show">
      <div v-for="(msg, index) in messages" :key="index">
        {{msg}}
      </div>
  </div>
</template>

<script>
export default {
    props:{
        value:{
            type:Object,
        }
    },
    computed:{
        messages(){
            return this.value.result.messages;
        },
        show(){
            var result = this.value.result
            return result.invalid && result.messages && result.messages.length > 0;
        }
    }
}
</script>

<style>
    .invalid-message{
        color:#F22;
        font-size:1rem;
    }
</style>